<template>
  <div class="wrapper">
    <div class="info-box">
      <div class="head">
        <span class="theme-title gradient-title">{{$t('calc.mt')}}</span>
      </div>
      <div class="des">
        <div v-for="item in list" :key="item.address">
          <span class="label">{{item.address}}</span>
          <span class="value">{{item.level}}</span>
        </div>
      </div>
    </div>
<!--    <div class="info-box">-->
<!--      <div class="head">-->
<!--        <span class="title">{{$t('calc.ir')}}</span>-->
<!--        <span>-->
<!--          <span class="num">10000</span>-->
<!--          ALD-->
<!--        </span>-->
<!--      </div>-->
<!--      <div class="btn-wrapper">-->
<!--        <div class="rec-btn theme-btn">-->
<!--          <span>{{$t('calc.receive')}}</span>-->
<!--        </div>-->
<!--      </div>-->
<!--    </div>-->
  </div>
</template>

<script>
import { commonMixin } from '@/utils/mixins'

export default {
  mixins: [ commonMixin ],
  data () {
    return {
      list: []
    }
  },
  created () {
    if (this.address) {
      this.getList()
    }
  },
  methods: {
    onAddress () {
      this.getList()
    },
    getList () {
      this.$toast.loading({
        message: this.$t('tip.loading'),
        duration: 0,
        forbidClick: true
      })
      let that = this
      this.$ald.getChildren(this.address).then(async function (res) {
        let l = []
        let lCalls = res.map(r => that.$callAld.userLevel(r))
        let levels = await that.$callProvider.all(lCalls)
        for (let i = 0; i < res.length; i++) {
          l.push({
            address: that.formatAddress(res[i]),
            level: 'Lv' + levels[i].toString()
          })
        }
        that.list = l
        that.$toast.clear()
      }).catch(() => {
        this.$toast.clear()
      })
    },
    formatAddress (a) {
      let s = String(a)
      return s.substr(0, 10) + '...' + s.substr(s.length - 10, 8)
    }
  }
}
</script>

<style lang="less" scoped>
.info-box {
  margin: 12px 14px;
  background: #fff;
  border-radius: 5px;
  box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.08);
  .head {
    line-height: 45px;
    padding: 0 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .title, .theme-title {
      font-weight: bold;
    }
    .num {
      color: @theme-color;
    }
  }
  .des {
    padding: 0 12px;
    border-top: 1px solid #DEDEDE;
    div {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 47px;
    }
    .label {
      color: #666666;
      font-size: 15px;
      font-weight: 500;
    }
    .value {
      color: #111111;
      font-size: 15px;
      font-weight: bolder;
    }
  }
  .btn-wrapper {
    padding: 20px 0 25px;
  }
}
</style>
